import { Level } from '@/components/Level';
import { Clock } from '@/components/Clock';
import { Break } from '@/components/Break';
import { Round } from '@/components/Round';

export class Tournament {
  private rounds: Round[] = [];
  private defaultRounds: Round[] = [
    new Level(25, 0),
    new Level(50, 0),
    new Level(100, 0),
    new Break(20 * 60),
    new Level(200, 0),
    new Level(400, 0),
    new Level(700, 0),
    new Break(20 * 60),
    new Level(1500, 0),
    new Level(3000, 0),
    new Level(5000, 0),
    new Break(10 * 60),
    new Level(10000, 0, 10 * 60),
    new Level(20000, 0, 10 * 60),
    new Level(40000, 0, 10 * 60),
    new Level(70000, 0, 10 * 60),

  ];
  private levelDuration: number = 60 * 20;
  private clock: Clock = new Clock();
  private currentRoundDate: RoundData = {
    breakIndex: 0,
    levelIndex: 0,
    nextLevelIndex: 0,
    roundTimeLeft: 0,
    roundIndex: 0,
    nextLevelIn: 0,
    breakIn: 1,
    breaksLeft: 0,
    breaksDone: 0,
    levelNumber: 1
  };

  private interval?: number;

  constructor(clock?: Clock, rounds?: (Level | Break)[], levelDuration?: number) {
    this.levelDuration = levelDuration || this.levelDuration;
    this.rounds = rounds || this.defaultRounds;
    this.clock = clock || this.clock;
    if (this.clock.isStarted) {
      this.start();
    }
  }

  private getCurrentRoundData(): RoundData {
    const duration = this.clock.secondsRunning();
    let roundsTotalTime = 0;
    let nextLevelFound = false;
    let roundFound = false;
    let breakFound = false;
    let data: RoundData = {
      levelNumber: 1,
      roundTimeLeft: 0,
      breakIndex: 0,
      roundIndex: 0,
      levelIndex: 1,
      nextLevelIndex: 1,
      nextLevelIn: 0,
      breakIn: 0,
      breaksLeft: 0,
      breaksDone: 0
    };

    for (let i = 0; i < this.rounds.length; i++) {
      let currentRoundDuration = this.rounds[i].duration || this.levelDuration;
      roundsTotalTime += currentRoundDuration;

      if (!roundFound && duration < roundsTotalTime) {
        roundFound = true;
        data.roundIndex = i;
      }

      if (this.rounds[i] instanceof Level) {
        if (!roundFound) {
          data.levelNumber++;
        } else if (data.roundIndex === i) {
          data.levelIndex = i;
          data.roundTimeLeft = roundsTotalTime - duration;
        } else if (!nextLevelFound) {
          nextLevelFound = true;
          data.nextLevelIndex = i;
          data.nextLevelIn = roundsTotalTime - duration - currentRoundDuration;
        }
      } else {
        if (!breakFound) {
          if (!roundFound) {
            data.breaksDone++;
          } else {
            breakFound = true;
            data.breakIndex = i;
            if (data.roundIndex === i) {
              data.breakIn = 0;
              data.roundTimeLeft = roundsTotalTime - duration;
            } else {
              data.breakIn = roundsTotalTime - duration - currentRoundDuration;
              data.breaksLeft++;
            }
          }
        } else {
          data.breaksLeft++;
        }
      }
    }
    return data;
  }

  public getRounds(): Round[] {
    return this.rounds;
  }

  public get timeRunning(): number {
    return this.clock.secondsRunning();
  }

  public get currentRoundIndex(): number {
    return this.currentRoundDate.roundIndex;
  }

  public get currentLevelNumber(): number {
    return this.currentRoundDate.levelNumber;
  }

  public get currentLevel(): Level {
    return this.rounds[this.currentRoundDate.levelIndex] as Level;
  }

  public get nextLevel(): Level {
    return this.rounds[this.currentRoundDate.nextLevelIndex] as Level;
  }

  public get currentRoundTimeLeft(): number {
    return this.currentRoundDate.roundTimeLeft;
  }

  public get nextLevelIn(): number {
    return this.currentRoundDate.nextLevelIn;
  }

  public get isBreak(): boolean {
    return this.currentRoundDate.breakIndex === this.currentRoundDate.roundIndex;
  }

  public get nextBreakIn(): number {
    return this.currentRoundDate.breakIn;
  }

  public get hasBreaksLeft(): boolean {
    return this.currentRoundDate.breaksLeft > 0;
  }

  public get isPaused(): boolean {
    return this.clock.isPaused;
  }

  public get isStarted(): boolean {
    return this.clock.isStarted;
  }

  public start() {
    if (!this.interval) {
      this.interval = setInterval(() => {
        this.currentRoundDate = this.getCurrentRoundData();
      }, 1000);
    }
    this.clock.start();
    Clock.toLocalStorage('tournament-clock', this.clock);
  }

  public reset(): void {
    this.clock = new Clock();
    Clock.toLocalStorage('tournament-clock', this.clock);
  }

  public pause() {
    this.clock.pause();
  }

  public updateLevelDuration(duration: number): void {
    if (!this.isStarted) {
      this.levelDuration = duration;
      this.currentRoundDate = this.getCurrentRoundData();
    }
  }
}

interface RoundData {
  roundIndex: number,
  roundTimeLeft: number,
  levelIndex: number,
  nextLevelIndex: number,
  breakIndex: number,
  levelNumber: number,
  breakIn: number,
  nextLevelIn: number,
  breaksLeft: 0,
  breaksDone: 0,
}
