export function secondsToHours(secondsTotal: number): any {
  const hours = Math.floor(secondsTotal / 60 / 60 / 24);
  const hoursString = hours > 9 ? hours : `0${hours}`;
  return `${hoursString}:${secondsToMinutes(secondsTotal)}`;
}

export function secondsToMinutes(secondsTotal: number): any {
  const minutes = Math.floor(secondsTotal / 60 % 60);
  const seconds = Math.floor(secondsTotal % 60);
  const minutesString = minutes > 9 ? minutes : `0${minutes}`;
  const secondsString = seconds > 9 ? seconds : `0${seconds}`;

  return `${minutesString}:${secondsString}`;
}
