











































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import { Tournament } from '@/components/Tournament';
import { Level } from '@/components/Level';
import { Clock } from '@/components/Clock';
import { secondsToHours, secondsToMinutes } from '@/components/filters';

@Component({
  filters: {
    secondsToHours,
    secondsToMinutes,
  }
})
export default class HelloWorld extends Vue {
  public tournament: Tournament = new Tournament(Clock.fromLocalStorage('tournament-clock'));

  public get isBreak() {
    return this.tournament.isBreak;
  }

  public get roundIndex(): number {
    return this.tournament.currentRoundIndex;
  }

  public get level(): Level {
    return this.tournament.currentLevel;
  }

  public get nextLevel(): Level {
    return this.tournament.nextLevel;
  }

  public get levelNumber(): number {
    return this.tournament.currentLevelNumber;
  }

  public get smallBlind(): number {
    return this.level ? this.level.smallBlind : 0;
  }

  public get bigBlind() {
    return this.level ? this.level.bigBlind : 0;
  }
}
