export class Clock {
  private startTime: number = 0;
  private pauseStartTime: number = 0;
  private totalPauseDuration: number = 0;

  constructor(config?: { startTime: number, pauseStartTime: number, totalPauseDuration: number }) {
    if (config) {
      this.startTime = config.startTime;
      this.pauseStartTime = config.pauseStartTime;
      this.totalPauseDuration = config.totalPauseDuration;
    }
  }

  public get isStarted(): boolean {
    return this.startTime !== 0;
  }

  public get isPaused(): boolean {
    return this.pauseStartTime !== 0;
  }

  public secondsRunning(): number {
    if (this.isPaused) {
      return Math.floor((Date.now() - (this.startTime + this.totalPauseDuration) - (Date.now() - this.pauseStartTime)) / 1000);
    } else {
      return Math.floor((Date.now() - (this.startTime + this.totalPauseDuration)) / 1000);
    }
  }

  public start(): void {
    this.stopPause();
    if (this.startTime === 0) {
      this.startTime = Date.now();
    }
  }

  public pause(): void {
    if (this.pauseStartTime === 0) {
      this.pauseStartTime = Date.now();
    }
  }

  public static toLocalStorage(name: string, clock: Clock) {
    localStorage.setItem('vue-clock.' + name, JSON.stringify({
      startTime: clock.startTime,
      pauseStartTime: clock.pauseStartTime,
      totalPauseDuration: clock.totalPauseDuration
    }));
  }

  public static fromLocalStorage(name: string): Clock | undefined {
    const item = localStorage.getItem('vue-clock.' + name);
    if (item) {
      try {
        return new Clock(JSON.parse(item));
      } catch (e) {
        console.error(e);
      }
    }

    return new Clock();
  }

  private stopPause(): void {
    if (this.pauseStartTime !== 0) {
      this.totalPauseDuration += Date.now() - this.pauseStartTime;
      this.pauseStartTime = 0;
    }
  }
}
